import { SfSteps, SfButton } from '@storefront-ui/vue';
import CartPreview from '~/components/checkout/CartPreview';
import OrderReview from '~/components/checkout/OrderReview';
import { ref, computed } from '@vue/composition-api';
import { useUser } from '@vue-storefront/commercetools';

const STEPS = {
  'personal-details': 'Personal Details',
  shipping: 'Shipping',
  payment: 'Payment',
  'order-review': 'Review'
};

export default {
  name: 'Checkout',
  components: {
    SfButton,
    SfSteps,
    CartPreview,
    OrderReview
  },
  setup(props, context) {
    const currentStep = computed(() => context.root.$route.path.split('/').pop());
    const { isAuthenticated } = useUser();
    const showCartPreview = ref(true);
    const currentStepIndex = computed(() => Object.keys(STEPS).findIndex(s => s === currentStep.value));
    const isThankYou = computed(() => currentStep.value === 'thank-you');

    const handleStepClick = (stepIndex) => {
      const key = Object.keys(STEPS)[stepIndex];
      context.root.$router.push(`/checkout/${key}`);
    };

    return {
      handleStepClick,
      STEPS,
      currentStepIndex,
      isThankYou,
      currentStep,
      showCartPreview,
      isAuthenticated
    };
  }
};
